	module.exports = {"data":{"post":{"html":"<p>It's been nearly two weeks since management dropped da bomb, and they still have not given an explanation for their actions, and the topic not even mentioned to stockholders at the annual shareholders meeting this week. There are a lot of rumours and speculation floating around the water cooler. The only thing that seems for sure is that a workers' council <i>(Betriebsrat)</i> will be formed.</p>\n<p>However, a TV will be set up so those of us who are still working can watch  German World Cup matches during working hours.</p>","fields":{"title":"Layoffs Update 2","date":"30 May 2002","url":"https://papascott.de/2002-05-30-layoffs-update-2/"}}},"pathContext":{"slug":"/2002-05-30-layoffs-update-2/","prev":{"fields":{"title":"BlogMap","date":"30 May 2002","slug":"/2002-05-30-blogmap/"}},"next":{"fields":{"title":"Don't Read This Book, Film At 11","date":"31 May 2002","slug":"/2002-05-31-dont-read-this-book-film-at-11/"}}}}

